import React, { useState } from "react";
import "./App.css";
import { useForm } from "react-hook-form";
import image from "./image0.png";
import axios from "axios";
const url = "https://contratax.herokuapp.com/";

function App() {
	const [fees, setFees] = useState("");
	const { register, handleSubmit } = useForm();
	const onSubmit = async (data) => {
		console.log(data);
		const response = await axios.post(url, { price: data.price, category: data.category });
		console.log(response);
		setFees(response.data);
	};
	return (
		<div className="form">
			<img className="contratax" src={image} alt="logo" />
			<form onSubmit={handleSubmit(onSubmit)}>
				<input className="input" type="number" placeholder="price" name="price" {...register("price")} />
				<label htmlFor="category"> </label>
				<select className="select" id="category" name="category" {...register("category")}>
					<option value="mobile">mobile</option>
					<option value="laptop">laptop</option>
				</select>
				<input type="submit" />
			</form>
			<div className="output">
				<p> {fees} </p>
			</div>
		</div>
	);
}

export default App;
